import React from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames';

const Text = ({ classes, children, color, family, letterSpacing, size, styles, weight }) => {
	let currentColor, currentFamily, currentType;

	// Color
	if (color === 'light') currentColor = classes.colorLight;
	else if (color === 'active') currentColor = classes.colorActive;
	else currentColor = classes.colorDark;

	// Font Family
	currentFamily = family === 'primary' ? classes.montserrat : classes.righteous;

	// Font Weight
	if (weight === 'light' || weight === '400') currentType = classes.light;
	else if (weight === 'medium' || weight === '500') currentType = classes.medium;
	else if (weight === 'bold' || weight === '700') currentType = classes.bold;
	else if (weight === 'extra-bold' || weight === '800') currentType = classes.extraBold;
	else currentType = classes.normal;

	let mainClass = classnames(currentColor, currentFamily, currentType);

	return (
		<p className={mainClass} style={{ fontSize: `${size}px`, letterSpacing: `${letterSpacing}px`, ...styles }}>
			{children}
		</p>
	);
};

Text.defaultProps = {
	color: 'dark',
	family: 'primary',
	letterSpacing: '1',
	size: '18',
	styles: {},
	weight: 'normal',
};

Text.propTypes = {
	children: PropTypes.node.isRequired,
	classes: PropTypes.object.isRequired,
	color: PropTypes.string,
	family: PropTypes.string,
	letterSpacing: PropTypes.string,
	size: PropTypes.string,
	styles: PropTypes.object,
	weight: PropTypes.string,
};

export default Text;
