import { IMAGES } from '../../configs';

const Styles = () => ({
	content: {
		alignItems: 'flex-end',
		display: 'flex'
	},
	image: {
		backgroundImage: `url('${IMAGES.PHOTO}')`,
		backgroundPosition: 'center',
		backgroundRepeat: 'no-repeat',
		backgroundSize: 'cover',
		borderRadius: '50%',
		maxHeight: '371px',
		maxWidth: '371px',
		minHeight: '371px',
		minWidth: '371px',
		zIndex: 100
	},
	textBox: {
		'& > div': {
			width: 'fit-content'
		},
		backgroundColor: '#fff',
		borderRadius: '10px',
		marginLeft: '-200px',
		maxWidth: '670px',
		minWidth: '670px',
		padding: '24px 34px',
	},
	root: {
		alignItems: 'center',
		backgroundImage: `url('${IMAGES.BACKGROUND_PRIMARY}')`,
		backgroundPosition: 'center',
		backgroundRepeat: 'no-repeat',
		backgroundSize: 'cover',
		display: 'flex',
		justifyContent: 'center',
		minHeight: '100vh',
	},
});

export default Styles;
