const Styles = () => ({
	root: {
		margin: '0 50px',
		paddingTop: '50px',

		'& > nav.navbar': {
			'& > .navbar-brand': {
				fontFamily: 'Righteous',
				fontSize: '36px',
			},
			'& > div > ul > li': {
				margin: '0 20px',

				'&:first-child': {
					marginLeft: 0
				},
				'&:last-child': {
					marginRight: 0
				},
				'& > a': {
					color: '#fff',
					fontFamily: 'Righteous',
					fontSize: '24px',
	
					'&:hover' : {
						color: '#239580',
						textDecoration: 'none'
					}
				}
			}
		},
	},
});

export default Styles;
