const Styles = () => ({
	light: theme('#fff', 'transparent', '2px solid #fff'),
	primary: theme('#fff', '#239580', 'none'),
	secondary: theme('#239580', '#fff', 'none'),
});

const theme = (color, backgroundColor, border) => ({
	color,
	backgroundColor,
	border,
	borderRadius: '10px',
	cursor: 'pointer',
	fontFamily: 'Montserrat',
	fontSize: '20px',
	fontWeight: 'bold',
	padding: '18px 34px',
	width: 'fit-content',
});

export default Styles;
