import React, { Fragment } from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames';
import { Link } from 'react-router-dom';
import { ICONS } from '../../../configs';

const Navbar = ({ classes, fixed, logo, title, titlePosition }) => {
	const items = [
		{ link: '/', label: 'Home' },
		{ link: '/about', label: 'About' },
		{ link: '/portfolio', label: 'Portfolio' },
		{ link: '/blog', label: 'Blog' },
		{ link: '/contact', label: 'Contact' },
	];

	const brand = () => {
		return (
			<Fragment>
				<Link to="/" className="navbar-brand">
					{logo && <img alt={logo} src={logo} />}
					{title}
				</Link>
				<button className="navbar-toggler" data-toggle="collapse" data-target="#NavbarMenu">
					<span className="navbar-toggler-icon"></span>
				</button>
			</Fragment>
		);
	};

	const menus = () => {
		let ulClass = 'navbar-nav';
		if (titlePosition === 'left') ulClass += ' ml-auto';

		return (
			<div className="collapse navbar-collapse" id="NavbarMenu">
				<ul className={ulClass}>
					{items.map((item, index) => (
						<li className="nav-item" key={index.toString()}>
							<Link to={item.link} className="nav-link">
								{item.label}
							</Link>
						</li>
					))}
				</ul>
			</div>
		);
	};

	let mainClass = classnames(classes.root, fixed ? fixed === 'top' ? 'fixed-top' : 'fixed-bottom' : '' )

	return (
		<div className={mainClass}>
			<nav className="navbar navbar-expand-lg border-0">
				{titlePosition === 'left' ? (
					<Fragment>
						{brand()}
						{menus()}
					</Fragment>
				) : (
					<Fragment>
						{menus()}
						{brand()}
					</Fragment>
				)}
			</nav>
		</div>
	);
};

Navbar.defaultProps = {
	fixed: '',
	logo: ICONS.TITLE,
	title: '',
	titlePosition: 'right',
};

Navbar.propTypes = {
	classes: PropTypes.object.isRequired,
	fixed: PropTypes.string,
	logo: PropTypes.string,
	title: PropTypes.string,
	titlePosition: PropTypes.string,
};

export default Navbar;
