import React, { Fragment } from 'react';
import PropTypes from 'prop-types';

// Components
import Text from '../../components/elements/Text';

const About = ({ classes }) => {
	return (
		<div className={classes.root}>
			<div className={classes.content}>
				<div className={classes.image} />
				<div>
					<div className="w-100" style={{ paddingLeft: '25px' }}>
						<Text color="light" family="righteous" size="72" weight="600">Hi, there</Text>
					</div>
					<div className={classes.textBox}>
						<div className="ml-auto">
							<Text styles={{ width: '400px' }} weight="light">“I’m an full stack developer, and i’m passionated person, self learner and love challenge. Share your vision and I’ll help your vision come true.“</Text>
							<Text weight="bold">Nuku Malik Sanjaya Kusuma</Text>
							<Text weight="light" styles={{ marginBottom: 0 }}>JavaScript Developer at SoftwareSeni</Text>
						</div>
					</div>
				</div>
			</div>
		</div>
	);
};

About.propTypes = {
	classes: PropTypes.object.isRequired,
};

export default About;
