import { IMAGES } from '../../configs';

const Styles = () => ({
	root: {
		backgroundImage: `url(${IMAGES.BACKGROUND_DARK})`,
		backgroundPosition: 'center',
		backgroundRepeat: 'no-repeat',
		backgroundSize: '101% auto',
		minHeight: '100vh',
	},
	content: {
		margin: '0 auto',
		textAlign: 'center',
		width: 'fit-content',
	},
});

export default Styles;
