import React from 'react';
import PropTypes from 'prop-types';

const Button = ({ children, classes, onClick, styles, type }) => {
	let currentType;
	if (type === 'light') currentType = classes.light;
	else if (type === 'secondary') currentType = classes.secondary;
	else currentType = classes.primary;

	return (
		<div className={currentType} style={styles} onClick={onClick}>
			{children}
		</div>
	);
};

Button.defaultProps = {
	onClick: () => {},
	styles: {},
	type: 'primary',
};

Button.propTypes = {
	children: PropTypes.node.isRequired,
	classes: PropTypes.object.isRequired,
	onClick: PropTypes.func,
	styles: PropTypes.object,
	type: PropTypes.string,
};

export default Button;
