import React from 'react';
import PropTypes from 'prop-types';
import { ICONS } from '../../configs';

// Components
import Button from '../../components/elements/Button';
import Navbar from '../../components/elements/Navbar';
import Text from '../../components/elements/Text';

const Homepage = ({ classes }) => {
	const Title = () => {
		const text = ['Do you looking for', 'Full Stack Developer?'];
		return (
			<div className={classes.title}>
				{text.map((item) => (
					<Text color="light" key={item} size="80" weight="800">
						{item}
					</Text>
				))}
			</div>
		);
	};

	return (
		<div className={classes.root}>
			<Navbar logo={ICONS.TITLE} />
			<div className={classes.content} style={{ marginTop: '120px' }}>
				{Title()}
				<Text color="active" letterSpacing="7" size="28" styles={{ marginBottom: '80px' }}>
					Web Design / Front End / Back End
				</Text>
				<div className="d-flex justify-content-center">
					<Button type="primary" styles={{ marginRight: '40px' }}>
						More Info
					</Button>
					<Button type="light">Hire Me</Button>
				</div>
			</div>
		</div>
	);
};

Homepage.propTypes = {
	classes: PropTypes.object.isRequired,
};

export default Homepage;
